<template>
  <div class="container">
    <h2>使用兩個參數透過SHA3_256加密</h2>

    <div class="input-group">
      <label for="param1">參數1:</label>
      <input
        type="text"
        v-model="param1"
        id="param1"
        placeholder="請輸入參數1"
      />
      <span v-if="param1Error" class="error-message">參數1不可為空</span>
    </div>

    <div class="input-group">
      <label for="param2">參數2:</label>
      <input
        type="text"
        v-model="param2"
        id="param2"
        placeholder="請輸入參數2"
      />
      <span v-if="param2Error" class="error-message">參數2不可為空</span>
    </div>

    <button @click="sendData1" class="send-button">加密</button>

    <p class="response-message" v-if="sha3RespMsg">
      SHA3加密後字串: {{ sha3RespMsg }}
    </p><hr />


    
    <h2>使用三個參數透過HMAC加密</h2>

    <div class="input-group">
      <label for="param3">參數1:</label>
      <input
        type="text"
        v-model="param3"
        id="param3"
        placeholder="請輸入參數1"
      />
      <span v-if="param3Error" class="error-message">參數1不可為空</span>
    </div>

    <div class="input-group">
      <label for="param4">參數2:</label>
      <input
        type="text"
        v-model="param4"
        id="param4"
        placeholder="請輸入參數2"
      />
      <span v-if="param4Error" class="error-message">參數2不可為空</span>
    </div>

    <div class="input-group">
      <label for="param5">參數3:</label>
      <input
        type="text"
        v-model="param5"
        id="param5"
        placeholder="請輸入參數3"
      />
      <span v-if="param5Error" class="error-message">參數3不可為空</span>
    </div>

    <button @click="sendData2" class="send-button">加密</button>

    <p class="response-message" v-if="hmacRespMsg">
      HMAC加密後字串: {{ hmacRespMsg }}
    </p>
  </div>
</template>

<script>
import axios from "axios";

export default {
  data() {
    return {
      param1: "",
      param2: "",
      param3: "",
      param4: "",
      param5: "",
      sha3RespMsg: "",
      hmacRespMsg: "",
      param1Error: false,
      param2Error: false,
      param3Error: false,
      param4Error: false,
      param5Error: false,
    };
  },
  methods: {
    async sendData1() {
      this.resetErrors();

      const isValid = this.validateParams(["param1", "param2"]);
      if (!isValid) return;

      try {
        const response = await axios.post("http://localhost:8080/api/sha3_256", {
          param1: this.param1,
          param2: this.param2,
          
        });
        this.sha3RespMsg = response.data;

      } catch (error) {
        console.error("發生錯誤:", error);
      }
    },
    async sendData2() {
      this.resetErrors();

      const isValid = this.validateParams(["param3", "param4", "param5"]);
      if (!isValid) return;

      try {
        const response = await axios.post("http://localhost:8080/api/hmac", {
          param3: this.param3,
          param4: this.param4,
          param5: this.param5,
        });
        this.hmacRespMsg = response.data;

      } catch (error) {
        console.error("發生錯誤:", error);
      }
    },
    validateParams(params) {
      let isValid = true;
      params.forEach((param) => {
        if (this[param].trim() === "") {
          this[`${param}Error`] = true;
          isValid = false;
        }
      });

      return isValid;
    },
    resetErrors() {
      for (let i = 1; i <= 5; i++) {
        this[`param${i}Error`] = false;
      }
    },
  },
};
</script>

